    <div @click="fire" class="filter-item" :class="forFilter" :style="`background-image:url(${imgurl})`">
        <!-- <slot name="a"></slot> slot으로 슬롯을 확보 Html 바인딩 할 때만 사용, 속성으론 사용불가 -->
        <!-- <slot name="b"></slot> -->
        <slot></slot>
    </div>
</template>

<script>
export default {
    name : 'filterItems',
    data (){
        return {

        }
    },

    methods : {
