            this.emitter.emit('userSelectFilter',this.forFilter) //selectedFilter라는 이름으로 this.forFilter란 데이터를 넘김 >> Container.vue 확인
        }
    },

    props : {
        imgurl : Object,
        forFilter : Array,
        // index: Number,
    },
}
</script>

<style>
.filter-item {
    width: 100px;
    height: 100px;
    margin: 10px 10px 10px auto;
