<template>
  <div>
    <div v-if="step == 0">
      <Post :instaData="postData" v-for="(postData, index) in instaData" :key="index" />
    </div>
    <!-- <Post :instaData="instaData"/> -->

    <!-- 필터선택페이지 -->
    <div v-if="step == 1">
      <div class="upload-image" :class="selectedFilter" :style="`background-image:url(${imgurl})`"></div>
      <div class="filters">
        <FilterBox v-for="(forFilter, index) in filters" :key=(index) :imgurl="imgurl" :filters="filters" :forFilter="forFilter">
          <!-- 슬롯에 단일 데이터 보낼 때 -->
           <span>{{forFilter}}</span>

          <!-- 슬롯이 어려개일 때
          <template v-sloat:a>데이터1</template>
          <template v-sloat:b>데이터2</template>
          -->
        </FilterBox>
      </div>
    </div>

    <!-- 글작성페이지 -->
    <div v-if="step == 2">
      <div class="upload-image" :class="selectedFilter"  :style="`background-image:url(${imgurl})`"></div>
      <div class="write">
        <textarea class="write-box" @input="$emit('write', $event.target.value)">write!</textarea>
      </div>
    </div>
  </div>
</template>

<script>
import Post from './Post.vue'
import FilterBox from './FilterBox.vue'

export default {
  name: 'Container',
  data() {
    return {
      filters: ["aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson",
        "inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua",
        "reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"],
      selectedFilter : '',
    }
  },
  components: {
    Post,
    FilterBox,
  },
  props: {
    instaData: Array,
    step: Number,
    imgurl: String,
  },

  mounted() {
    this.emitter.on('userSelectFilter', (userSelectFilter) => {
      this.selectedFilter = userSelectFilter
    })
    //FilterVix.vue에서 전달한 emit을 selectedFilter라는 이름으로 받고, 그 안의 데이터를 파라미터로 받는다
    // 파라미터로 받을때 인자값 작명은 자유
  }
}
</script>

<style type="text/css">
.upload-image {
  width: 100%;
  height: 450px;
  background: cornflowerblue;
  background-size: cover;
}

.filters {
  overflow-x: scroll;
  white-space: nowrap;
}

.filter-1 {
  width: 100px;
  height: 100px;
  background-color: cornflowerblue;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
}

.filters::-webkit-scrollbar {
  height: 10px;
  cursor: pointer;
}

.filters::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.filters::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}

.filters::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.write-box {
  border: none;
  width: 90%;
  height: 100px;
  padding: 15px;
  margin: auto;
  display: block;
  outline: none;
}
</style>