<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step==2" @click="publish">발행</li>
      <li v-else @click="step++">Next</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>
  <h4>store사용 {{ $store.state.name }}</h4>

  <!-- app.use(store)를 통해 참조하기로한 vuex 데이터셋에서 state(data())와 같은 저장소 안에 name이라는 DB를 추출 -->
  <button @click="$store.state.name = '박'">변경</button>

  <!-- 버튼으로 app.use(store).state.name에 접근해서 값 변경이 가능하지만, 되도록 변경하면안됨 -->

  <h4>{{ $store.state.age }}</h4>
  <button @click="$store.commit('agePlusPlus', 10)">나이 1씩 증가</button>
  <!-- state에 직접 관여해서 값을 변경하는건 형상관리가 어려우니 commit을 통해 mutatuions 규칙을 불러온다 -->

  <p>{{ $store.state.more }}</p>
  <button class="more" @click="$store.dispatch('getData')">More</button>
  <!-- ajax로 불러온 actions를 불러올 때 dispatch를 사용한다. -->

  <Container :instaData="instaData" :step="step" :imgurl="imgurl" @write="작성한글 = $event"/>
  <p>{{ now() }}</p>
  <button>
    더보기
  </button>

  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" multiple type="file" id="file" class="inputfile" accept=".gif,.jpg,.jpeg,.png,.pdf"/>
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>
</template>



<script>
import Container from './components/Container.vue';
import instaData from './assets/instaData.js'
import axios from 'axios' //axios로 ajax 요청하는법


export default {
  name: 'App',
  data() {
    return {
      instaData : instaData,
      step: 0, // 현재 보여줄 페이지 index
      imgurl : '',
      작성한글: '',
      selectedFilter: '',
    }
  },

  computed : {
    now2() {
      return new Date()
    }, //computed 함수는 함수를 사용해도 실행되지 않고, 처음 실행하고 값을 저장함.
  },

  methods : {
    now() {
      return new Date()
    }, // methods 함수는 해당 함수를 사용할 때마다 실행됨.

    more () {
      axios.get(`https://codingapple1.github.io/vue/more0.json`) //get 요청으로 데이터 불러오기에 성공시 실행할 코드 //axios.post('URL',{name : 'kim'}).then() == 서버 데이터를 보낼 때, 성공 시 then / 실패 시 catch
      .then(결과 => {
        this.instaData.push(결과.data);
      })
    },

    upload(e) {
      let file = e.target.files; //html에서 change를 통해 input의 상태가 변경될 때 upload란 함수를 실행시키고, file이라는 변수에 해당 event 타겟의 files의 정보를 담는다.
      let url = URL.createObjectURL(file[0]);
      // FileReadir()를 사용하면 파일을 text로 변환해줌, 그 text를 src로 뿌려주는 방법 1
      // URL.createObjectURL()을 사용하여 이미지의 가상 URL을 호스트해줌 > 그 주소를 사용하면됨
      this.imgurl = url;
      this.step++; // 업로드가 된 후에 다음 스텝으로 이동하기 위해 step에 1을 할당했고, Container 컴포넌트에 각각 스텝 숫자가 0, 1, 2에따라 영역이 바뀌도록 세팅해놨다
    },
    // 컴퓨터안의 모든 파일은 0과 1로 구성된 binary데이터인데, BLOB는 Binary Large Object인데 BLOB이라는 곳에 담겨진다.
    publish() {
      var userPosted = {
        name: "Kim Hyun",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.imgurl,
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.작성한글,
        filter: this.selectedFilter,
      };
      this.instaData.unshift(userPosted);
      this.step=0;
    },
  },

  components: {
    Container,
  },

  mounted() {
    this.emitter.on('userSelectFilter', (userSelectFilter) => {
      this.selectedFilter = userSelectFilter
    })
  }
}

// get 요청을 통해 데이터를 전송받으면 화면이 새로고침 되는데 ajax를 통해 새로고침이 안되도록 할 수 있음
// ajax를 요청하려면 axios라이브러리를 쓰든가 기본 fetch 함수를 써야함.

</script>

