import axios from 'axios';
import { createStore } from 'vuex'

// App.vue에 모든 데이터가 집합돼있으면 의미가 없다.
// Vue.js에서 상태를 사용하는 이유는 여러 컴포넌트에서 동일한 데이터를 공유하고 변경할 수 있으며,
// 중앙 집중적으로 관리하여 코드의 간결성을 유지할 수 있기 때문입니다.
const store = createStore({
  state(){
    return {
      name : 'kim',
      age : 20,
      likes : 30,
      switch : false,
      more : {},
    }
  },
  mutations : {
    //state 수정 방법을 정의하는 곳, State 변경 권한은 모두 mutations에 있음 : 디버깅 용이
    stateNameChange(state) { //뮤테이션 안의 함수에 들어가는 파라미터는 항상 state를 뜻한다고 보면됨
        state.name = 'park';
    },
    agePlusPlus(state, data) { // 두번째 파라미터는 주체가 보낸 데이터를 받아서 씀
        state.age += data;
    },

    likesOrNot(state) {
        if(state.switch == false) {
            state.likes = state.likes + 1
            state.switch = true;
        } else {
            state.likes = state.likes - 1;
            state.switch = false;
        }
    },
    setMore(state, data) {
      state.more = data
      // Data를 받아오는 트리거 클릭 > actions를 통해 ajax 데이터를 로드 및 setMore 규칙을 발동 >
      // setMore에 전달된 json 데이터를 state.more에 저장
    }
  },
  actions : { //ajax하는 곳
    getData(context) { //context라는 파라미터가 있어야 commit이 가능 context=store
      axios.get(`https://codingapple1.github.io/vue/more0.json`)
      .then((a)=>{
        context.commit('setMore',a.data) // mutations 안의 setMore을 실행시키기 위한 절차, 함수실행 및 데이터 전달
      })
    }
     //axios 데이터를 받아서 a.data에 저장을 하는데, 이걸 state에 담아야 DB처럼 활용이 가능하다
     //근데 데이터를 받아와서 state에 담으려면 그 또한 state의 변경이 되므로 mutations를 거쳐야한다.
  },
})

export default store;